import { ChangeEvent, ComponentPropsWithoutRef, useCallback } from 'react';

import { AddIcon, MinusIcon } from '../../../assets';
import { DEFAULT_MAX_COUNT, DEFAULT_MIN_COUNT } from '../../../constants';
import { isNumber } from '../../../utils/validator';
import * as S from './StepperButton.styles';

interface StepperButtonProps extends ComponentPropsWithoutRef<'div'> {
  count: number;
  minCount?: number;
  maxCount?: number;
  handleCountChange: (count: number) => void;
}

const StepperButton = ({
  count,
  minCount = DEFAULT_MIN_COUNT,
  maxCount = DEFAULT_MAX_COUNT,
  handleCountChange,
  className,
  ...attributes
}: StepperButtonProps) => {
  const onChange = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      if (!isNumber(event.target.value)) return;

      const currCount = Number(event.target.value);

      if (currCount < minCount || currCount > maxCount) return;

      handleCountChange(currCount);
    },
    [handleCountChange, minCount, maxCount]
  );

  return (
    <S.StepperContainer className={`stepper-button ${className}`} {...attributes}>
      <S.StepperButton
        type="button"
        aria-label=" "
        disabled={count === minCount}
        variant="textButton"
        size="small"
        onClick={() => handleCountChange(count - 1)}
      >
        <MinusIcon />
      </S.StepperButton>
      <S.StepperInput
        name="count"
        value={count}
        aria-label=" "
        onChange={onChange}
      ></S.StepperInput>
      <S.StepperButton
        type="button"
        aria-label=" "
        disabled={count === maxCount}
        variant="textButton"
        size="small"
        onClick={() => handleCountChange(count + 1)}
      >
        <AddIcon />
      </S.StepperButton>
    </S.StepperContainer>
  );
};

export default StepperButton;
